$mobile: 375px;
$tablet: 768px;
$desktop: 1440px;

@keyframes vibrate {
  0% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }

  20% {
    -webkit-transform: translate(-2px, 2px);
    transform: translate(-2px, 2px);
  }

  40% {
    -webkit-transform: translate(-2px, -2px);
    transform: translate(-2px, -2px);
  }

  60% {
    -webkit-transform: translate(2px, 2px);
    transform: translate(2px, 2px);
  }

  80% {
    -webkit-transform: translate(2px, -2px);
    transform: translate(2px, -2px);
  }

  100% {
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}

.canvas {
  position: fixed;
  top: 0;
  width: 1440px;
  height: 760px;
  overflow: hidden;

  background: rgb(136, 204, 223);
  background: linear-gradient(
    180deg,
    rgba(136, 204, 223, 1) 0%,
    rgba(200, 233, 148, 1) 50%,
    rgba(9, 92, 36, 1) 100%
  );
  background-position: center;
}


#car {
  position: absolute;
  top: 63%;
  left: 45%;
  z-index: 12;
  width: 50px;
  height: auto;
  transform: scale(2);
  //  transform: scale(0.4) translate(-230%, -50%);

  @media screen and (min-width: $tablet) {
    top: 66%;
    left: 45%;
    transform: scale(2);
  }
  @media screen and (min-width: $desktop) {
    top: 70%;
    left: 42%;
    transform: scale(2.5);
  }
}
#car_turn_left {
  position: absolute;
  top: 63%;
  left: 45%;
  z-index: 12;
  width: 50px;
  height: auto;
  transform: scale(-3, 3);
  display: none;

  @media screen and (min-width: $tablet) {
    top: 66%;
    left: 45%;
    transform: scale(-3, 3);
  }
  @media screen and (min-width: $desktop) {
    top: 70%;
    left: 42%;
    transform: scale(-3.5, 3.5);
  }
}
#car_turn_left.active {
  left: 0;
  top: 90%;
  transition: all 5s cubic-bezier(0, 0, 1, 1);
}

